
import React,{Fragment} from 'react';
import '../../style/main.scss'
import {Component} from 'react';
import {Link, NavLink} from 'react-router-dom';
import profile from '../../img/profile.png'

export default class Navbar extends Component {
    render(){
        return(
            <Fragment>
                <nav>
                    <ul className="nav_menu">
                        <li className="nav_menu_list">
                            <NavLink to="/me" className="nav_menu_list_link" activeClassName="active"> Me </NavLink>
                        </li>
                        <li className="nav_menu_list">
                            <NavLink to="/work" className="nav_menu_list_link" activeClassName="active"> Work </NavLink>
                        </li>
                    </ul>
                    <div className="nav_logo">
                        <NavLink to="/" exact className="logo" activeClassName="aktif">
                            Logo
                        </NavLink>
                    </div>
                    <ul className="nav_menu">
                        <li className="nav_menu_list">
                            <NavLink to="/blog" className="nav_menu_list_link" activeClassName="active"> Blog </NavLink>
                        </li>
                        <li className="nav_menu_list">
                            <NavLink to="/contact" className="nav_menu_list_link" activeClassName="active"> Contact </NavLink>
                        </li>
                    </ul>
                </nav>
            </Fragment>
    )
}}

